.UsersPhoto{
    width: 100px;
    height: 100px;
    border-radius: 50px;
}



span{
    cursor: pointer;
}
.preloader{
    position: absolute;
    left: 50%;
    top:50%;
    width: 70px;
    height: 70px;
}

.paginator {
    margin:10px;
}

.pageNumber{
    padding: 2px;
    border: 1px solid grey;
}

.pageNumber.selectedPage {
    font-weight: bold;
    border-color: black;
}